.row {
  display: flex;
  align-items: center;
  justify-content: center;
}

.row > button {
  margin-left: 4px;
  margin-right: 8px;
}

.row:not(:last-child) {
  margin-bottom: 16px;
}

.value {
  font-size: 78px;
  padding-left: 16px;
  padding-right: 16px;
  margin-top: 2px;
  font-family: 'Courier New', Courier, monospace;
}

.button {
  appearance: none;
  background: none;
  font-size: 32px;
  margin: 6px;
  padding-left: 12px;
  padding-right: 12px;
  outline: none;
  border: 2px solid transparent;
  color: rgb(112, 76, 182);
  padding-bottom: 4px;
  cursor: pointer;
  background-color: rgba(112, 76, 182, 0.1);
  border-radius: 2px;
  transition: all 0.15s;
}

.textbox {
  font-size: 32px;
  padding: 2px;
  width: 64px;
  text-align: center;
  margin-right: 4px;
}

.button:hover,
.button:focus {
  border: 2px solid rgba(112, 76, 182, 0.4);
}

.button:active {
  background-color: rgba(112, 76, 182, 0.2);
}

.asyncButton {
  composes: button;
  position: relative;
}

.asyncButton:after {
  content: '';
  background-color: rgba(112, 76, 182, 0.15);
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  opacity: 0;
  transition: width 1s linear, opacity 0.5s ease 1s;
}

.asyncButton:active:after {
  width: 0%;
  opacity: 1;
  transition: 0s;
}

.container {
  margin-left: 12%;
  margin-right: 12%;
  padding-top: 2em;
}

.wrapper {
  font-family: 'Varela';
}

.title {
  font-family: 'Varela';
  /* font-family: 'Yiddish'; */
  /* font-family: 'DanaYad'; */
  /* font-family: 'Nunito'; */
  font-size: 36px;
  margin-top: 0;
  color: #2A323C;
}

.author {
  text-align: right;
  font-family: 'Nunito';
  margin-right: 6%;
  font-size: 16px;
  font-style: italic;
  color: #5C6269;
}

.context {
  text-align: justify;
  /* font-family: 'Nunito'; */
  color: #373a3d;
  font-size: 18px;
}

.tagsContainer {
  text-align: end;
  margin-top: 3em;
  margin-bottom: 12px;
}

.tagsContainer div{
  margin: 4px
}

.contextRtl p {
  direction: rtl;
  text-align: justify!important;
}

.contextLtl p {
  direction: ltr;
  text-align: justify!important;
}

.buttonGroup {
  margin-top: 2em;
  margin-bottom: 2em;
  border: none
}

.buttonGroup button{
  font-style: normal!important;
  font-family: Nunito!important;
  font-weight: 600!important;
  line-height: 16px!important;
  color: #1871E8!important;
  border: none
}