.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-float infinite 3s ease-in-out;
  }
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}


.App-container {
  min-height: calc(100vh - 64px);
  /* display: flex;
  flex-direction: column; */
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: rgb(112, 76, 182);
}

@keyframes App-logo-float {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(0px);
  }
}

@font-face {
  font-family: 'DanaYad';
  src: local('DanaYad'), url(./fonts/DanaYadAlefAlefAlef-Normal.woff) format('woff');
}

@font-face {
  font-family: 'Yiddish';
  src: local('Yiddish'), url(./fonts/YiddishkeitAlefAlefAlef-Bold.woff) format('woff');
}

@font-face {
  font-family: 'Varela';
  src: local('Varela'), url(./fonts/VarelaRound-Regular.ttf) format('truetype');
}
